"use client"

import { useGetCurrentUser, useUpdateUser } from "@/api/auth/useAuth"
import { authStore } from "@/store/authStore"
import { AxiosError } from "axios"
import Image from "next/image"
import { useEffect } from "react"
import { useForm } from "react-hook-form"
import toast from "react-hot-toast"

type Inputs = {
  id: number
  email: string
  username: string
  phone?: string
}

const AccountTab = () => {
  const { setUser } = authStore()
  const { data } = useGetCurrentUser()
  const { mutate: updateUser } = useUpdateUser()
  const userData = data
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors, isDirty },
  } = useForm<Inputs>()

  // Populate form with fetched data
  useEffect(() => {
    if (userData) {
      reset({
        id: userData.id,
        email: userData.email || "",
        username: userData.username || "",
        phone: userData.phone || "",
      })
      setUser(userData)
    }
  }, [userData, reset, setUser])

  const onSubmit = (formData: Inputs) => {

    if (!userData.id) return

    const updatePayload = {
      id: userData.id,
      // email: formData.email,
      username: formData.username,
      phone: formData.phone || "",
    }

    updateUser(updatePayload, {
      onSuccess: (data) => {
        toast.success(data.message || "user updated")
      },
      onError: (error) => {
        const err = error as AxiosError<{ message: string }>
        toast.error(err.response?.data?.message || "Something went wrong")
      },
    })
  }

  return (
    <div className='bg-[#1A1A1D] rounded-lg p-6 space-y-6'>
      <h2 className='text-xl font-semibold mb-4'>Profile Details</h2>

      <div className='flex items-start gap-4'>
        <Image alt='profile' src={require("../../assets/Images/profile.png")} />
        <div className='flex flex-row gap-5 mt-4'>
          {/* <div className='bg-[#242330] cursor-pointer hover:bg-white hover:text-black text-[#7367f0] text-center pt-2 rounded-lg h-10 w-44'>
            Upload new Photo
          </div> */}
          <div className='bg-[#323233] cursor-pointer hover:bg-purple-700 hover:text-white text-[#a8aaad] text-center pt-2 rounded-lg h-10 w-30'>
            Reset
          </div>
        </div>
      </div>

      <p className='text-sm text-gray-400'>
        Allowed JPG, GIF or PNG. Max size of 800K
      </p>

      <form
        onSubmit={handleSubmit(onSubmit)}
        className='grid grid-cols-1 md:grid-cols-2 gap-4'
      >
        <div className='space-y-2'>
          <label className='text-sm text-gray-400'>User Name</label>
          <input
            {...register("username", { required: true })}
            placeholder='Enter your username'
            className='w-full h-10 px-3 py-2 bg-[#212121] border border-[#3c3c3c] rounded-md text-white placeholder:text-gray-500 focus:outline-none focus:ring-2 focus:ring-purple-500 focus:border-transparent transition-colors'
          />
          {errors.username && (
            <p className='text-red-500 text-sm'>Username is required</p>
          )}
        </div>

        <div className='space-y-2'>
          <label className='text-sm text-gray-400'>Email</label>
          <input
            {...register("email")}
            readOnly
            disabled
            placeholder='Enter your email'
            className='w-full h-10 px-3 py-2 bg-[#2a2a2a] border border-[#3c3c3c] rounded-md text-gray-400 placeholder:text-gray-500 cursor-not-allowed'
          />
        </div>

        <div className='space-y-2'>
          <label className='text-sm text-gray-400'>Phone Number</label>
          <input
            {...register("phone")}
            placeholder='Enter your phone number'
            className='w-full h-10 px-3 py-2 bg-[#212121] border border-[#3c3c3c] rounded-md text-white placeholder:text-gray-500 focus:outline-none focus:ring-2 focus:ring-purple-500 focus:border-transparent transition-colors'
          />
        </div>

        <div className='col-span-2'>
          <button
            type='submit'
            className='bg-[#7367f0] hover:bg-purple-700 w-24 h-9 text-white rounded-md transition-colors'
            disabled={!isDirty}
          >
            Update
          </button>
        </div>
      </form>
    </div>
  )
}

export default AccountTab
